.inputField {
  padding: 6px;
  width: 100%;
  border: 1px solid #a6a6a6;
  border-radius: 3px;
  box-shadow: 0 1px 0 rgba(255, 255, 255, 0.5),
    0 1px 0 rgba(0, 0, 0, 0.07) inset;
  outline: 0;
}

input:focus,
textarea:focus {
  box-shadow: 0 0 5px #ff8c00;
  border: 1px solid #ff8c00;
}
